<template>
	<section>
		<div class="zx-banner">
      <img src="../../../static/images/zx_banner2.jpg" alt="">
      <ul class="zx-zl-dzsj">
        <li>
          <span>{{info.articleCount||0}}篇</span>
          <em>文章</em>
        </li>
        <li>
          <span>{{info.browseCount||0}}</span>
          <em>总阅读</em>
        </li>
        <li>&nbsp;</li>
        <li>
          <span>{{info.fabulousCount||0}}</span>
          <em>被赞</em>
        </li>
        <li>
          <span>{{info.subscribeCount||0}}</span>
          <em>订阅量</em>
        </li>
      </ul>
      <div class="zx-myho zx-zlbn">
        <span><img :src="info.columnCover" alt=""></span>
        <em>{{info.columnTitle}}<a href="javascript:;" class="zx-my-tgz">订阅专栏</a></em>
      </div>
    </div>
    
    <div class="inner">
      <div class="zx-zlyl-t">
        <h1>{{info.authorName}}</h1>
        <p>{{info.columnBrief}}</p>
      </div>
      <!--<div class="zx-zlyl-con">
        <p>这里是专家详情——这是一篇专门写给所有产品技术型CEO的文章，希望对你有所思考~enjoy~
       各位产品技术型CEO,大家好！一直想给你们写点东西，因为我做硅谷蓝图这件事很大原因是想帮助你...，一直想给你们写点东西，因为我做硅谷蓝图这件事很做硅谷！</p>
       <p>这里是专家详情——这是一篇专门写给所有产品技术型CEO的文章，希望对你有所思考~enjoy~
       各位产品技术型CEO,大家好！一直想给你们写点东西，因为我做硅谷蓝图这件事很大原因是想帮助你...，一直想给你们写点东西，因为我做硅谷蓝图这件事很做硅谷！</p>
       </div>-->
       <div class="zx-butt zx-xz-butt">
         <button type="button" class="zx-btn-bc zx-btn-fh" @click="goBack">返回</button>
        </div>
    </div>

	</section>
</template>

<script>
	import {columnDetails} from '../../api/info';
	export default{
		data(){
			return{
				id:'',
				info:{}
			}
		},
		methods:{
			init(){
				
				var param = {
					id:this.id
				}
				columnDetails(param).then(res=>{
					console.log(res)
					this.info=res.datas
				})
			},
			goBack(){
				if (navigator.userAgent.indexOf("MSIE") > 0) {     
			        if (navigator.userAgent.indexOf("MSIE 6.0") > 0) {     
			            window.opener = null; window.close();     
			        }     
			        else {     
			            window.open('', '_top'); window.top.close();     
			        }     
			    }     
			    else if (navigator.userAgent.indexOf("Firefox") > 0) {     
			        window.location.href = 'about:blank '; //火狐默认状态非window.open的页面window.close是无效的    
			        //window.history.go(-2);     
			    }     
			    else {     
			        window.opener = null;      
			        window.open('', '_self', '');     
			        window.close();     
			    }     
			}
		},
		mounted(){
			 
			 	this.id=this.$own.getHref('id')
				this.init();
		}
	}
</script>

